@import "var/colors";
@import "var/fonts";
@import "var/icons";

html {}

html * {}

body {
    font-size: 15px;
    color: $gray-1000;
    background: $white;
    font-family: $font-family-primary;
    overflow-x: hidden;
    position: relative;
    width: 100%;
    margin: 0 auto;
    transition: 0.3s ease transform;
}

body.--navUserActive,
body.--cartActive {
    overflow: hidden;
    padding-right: 17px;
}

body.mobile.--navUserActive,
body.mobile.--cartActive {
    padding-right: 0px;
}

@media screen and (max-width: 991px) {

    body.--menuActive,
    body.--searchActive {
        overflow: hidden;
    }

    body.--navUserActive,
    body.--cartActive {
        padding-right: 0px;
    }
}

@media screen and (min-width: 992px) {
    body.submenu-active {
        overflow-y: scroll;
    }
}

::selection {
    background: $purple-200;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

/* Firefox */
input[type=number] {
    -moz-appearance: textfield;
}

a {
    color: $purple-500;
    text-decoration: none;
    transition: 0.3s ease all;

    &:hover,
    &:active,
    &:focus {
        color: $gray-1000;
    }
}

h1 {
    font-size: 32px;
    font-family: $font-family-headings;
    font-weight: 500;
}

h2 {
    font-size: 30px;
    font-family: $font-family-headings;
    font-weight: 500;
}

