.no-display,
.js-hidden {
    display: none;
}

.js-hidden.visible {
    display: flex;
    flex-direction: column;
}

.sr-only {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: -1px;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    border: 0;
}


.overall-wrapper {
    display: flex;
    flex-direction: column;
}

.overall-wrapper>* {
    order: 100;
}

.overall-wrapper .top-navigation-bar {
    order: 0;
}

.overall-wrapper #header {
    order: 1;
}

.middle-banners-wrapper {

}

.instagram {}

.instagram h1 {
    font-size: 30px;
    margin-bottom: 20px;
    text-align: center;
}

#backToTop {
    display: none;
}

@media (max-width: 991px) {
    #backToTop {
        position: fixed;
        opacity: 0;
        pointer-events: none;
        bottom: 15px;
        right: 15px;
        background: $gray-900;
        color: white;
        font-size: 11px;
        height: 46px;
        width: 46px;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 9;
        transition: 0.3s ease opacity;

        span {
            border: solid $white;
            border-width: 0 2px 2px 0;
            display: inline-block;
            padding: 3px;
            transform: rotate(-135deg);
            margin-top: 4px;
        }
    }

    body.--floatSearchActive #backToTop {
        opacity: 1;
        pointer-events: auto;
    }

    body.--floatSearchActive #chat-application~#backToTop {
        bottom: 75px;
    }
}

